import {initBasePage} from './_init_page'
import userFn from '../animate/user_function'
import initFunction from '../animate'
const oldPage = Page;
let basePage = (options) => {

    if (!options.mixins) options.mixins = []


    options.mixins.push(userFn)
    options.mixins.push(initBasePage)
    options.mixins.push(initFunction)



    const mixins = options.mixins;
    options = setUser(options)

    if (Array.isArray(mixins)) {

        delete options.mixins;
        options = merge(mixins, options)
    }
    oldPage(options)
}
const originProperties = ['data', 'properties', 'options'];
const userContainer = ['throttle'];
const originMethods = ['onLoad', 'onReady', 'onShow', 'onHide', 'onUnload', 'onPullDownRefresh', 'onReachBottom', 'onShareAppMessage', 'onPageScroll', 'onTabItemTap']

function merge(mixins, options) {
    mixins.forEach((mixin) => {
        if (Object.prototype.toString.call(mixin) !== '[object Object]') {

            throw new Error('对象错误')
        }
        for (let [key, value] of Object.entries(mixin)) {
            if (originProperties.includes(key)) {

                options[key] = {...value, ...options[key]}
            } else if (originMethods.includes(key)) {
                const originFunc = options[key]
                options[key] = function (...args) {

                    value.call(this, ...args)
                    return originFunc && originFunc.call(this, ...args)
                }
            } else {

                options = {...mixin, ...options}
            }
        }
    })
    return options
}

function setUser(options) {
    if (options['throttle'] && typeof options['throttle'] == 'object') {

        const tharo = options['throttle'];
        const time = tharo.time || void 0
        for (let [key, value] of Object.entries(tharo)) {
            options[key] = userFn.throttle.call(this,function (...args) {
                value.call(this, ...args)
            }, time)
        }
    }

    return options
}
function throttle(fn,durTime=1500){
    let time=false;
    return function (){
        let that=this;
        let args=arguments;
        if(time){
            return false
        }else{
            fn.apply(that,args)
        }
        time=setTimeout(()=>{
            time=false
        },durTime)
    }
}
export {basePage}
