import error from '../../util/error'


export default {

     getHeight(argument){
        let text =argument;
        let query = wx.createSelectorQuery();
        query.select(text).boundingClientRect()
        return new Promise((resolve,reject)=>{
            query.exec((res)=>{
                if (res[0] != null) {
                    resolve(res)
                }else{
                    reject(
                        error.error(`输入的ID：${argument}未找到或不合法`)
                    )

                }
            })
        })
    },

    getNode(name,arr=[]){
        let property=['margin', 'backgroundColor','fontSize','color','padding'].concat(arr);
        return new Promise(resolve => {
            wx.createSelectorQuery().select(name).fields({
                dataset: true,
                size: true,
                scrollOffset: true,
                properties: ['scrollX', 'scrollY'],
                computedStyle: property,
                context: true,
                node:true
            }, res=>{
                resolve(res)
            }).exec()
        })
    },
    getWxImg(src){
         return new Promise((resolve,reject)=>{
             wx.getImageInfo({
                 src: src,
                 success: function (res) {
                     resolve(res)
                 },
                 fail:function(res){
                     reject(
                         error.error(`输入的src：${src}未找到或不合法`)
                     )
                 }
             })
         })

    },

    getNav(that, id = "#Navigation"){
        const navigation = that.selectComponent(id);
        if (navigation) {
            const navHeight = navigation.data.titleBarHeight;
            const navTopHeight = navigation.data.statusBarHeight;
            that.setData({ navHeight: navHeight + navTopHeight })
            return navHeight + navTopHeight
        }else{
            return false
        }
    },
    isIphoneX(that, otherHeight = 0,addDOM=true){
        return new Promise(resolve => {
            wx.getSystemInfo({
                success: function (res) {
                    let isIPX = Boolean;
                    let isIphone=Boolean;
                    let scrollHeight = Number;
                    if (res.model.indexOf("iPhone") > -1) {
                        if (res.model.indexOf('iPhone X')==-1) {
                            isIPX = false;
                            scrollHeight = res.windowHeight - otherHeight
                        } else {
                            isIPX = true;
                            scrollHeight = res.windowHeight - 34 - otherHeight
                        }
                        isIphone=true
                    } else {
                        isIPX = false;
                        scrollHeight = res.windowHeight - otherHeight
                        isIphone=false
                    }
                    //console.log(isIPX)
                    console.log("手机相关信息", res)
                    let resData={
                        isIPX: isIPX,
                        isIphone,
                        windowHeight: res.windowHeight,
                        windowWidth: res.windowWidth,
                        scrollHeight: scrollHeight,
                        scale:(res.windowHeight/res.windowWidth).toFixed(2)
                    }

                    if(addDOM){
                        that.setData(resData)
                    }
                    resolve(resData)
                }
            })
        })

    }
}