import {isUndefined,isObject} from '../../lib/init_lodash'

import utils from './utils'
const routerType=[
    {
        name:'switchTab',
        cname:'toTab'
    },
    {
        name:'reLaunch'
    },
    {
        name:'redirectTo',
        cname:'offTo'
    },
    {
        name:'navigateTo',
        cname:'to'
    },
    {
        name:'navigateBack',
        cname:'back'
    },
]


export default {
    $_toPath(e){
        const pageTo=isObject(e)&&!isUndefined(e.currentTarget);
        console.log(e,Object.prototype.toString.call(e),isObject(e),!isUndefined(e.currentTarget))
        let {path,toType,toData}=pageTo?utils.$_getData(e):e
        if(this.$_toPath_before!=undefined){
            let beforeData=this.$_toPath_before({path,toType,toData,pageTo})
            if(beforeData===false){
                return console.warn('进入路由拦截')
            }else if(!isUndefined(beforeData)){
                if(!isUndefined(beforeData.path)) path=beforeData.path;
                if(!isUndefined(beforeData.toType)) toType=beforeData.toType;
                if(!isUndefined(beforeData.toData)) toData=beforeData.toData;
            }
        }
        toType=routerType.find(x=>x.name==toType||x.cname==toType)
        console.log(toType)
        toData=toData==undefined?'':`?${utils.$_dataToString(toData)}`
        console.log(toData)
        console.log(path,toData)
        wx[toType.name]({
            url:path+toData
        })



    }

}
