
import util from './utils'
import router from './router'
import {isString} from '../../lib/init_lodash'

export default {
    $_setTimeFn(fn,time=500) {
        setTimeout(()=>{
            fn.apply(this,arguments)
        },time)
    },
    $_animateStart(e){
        let hideTime,showTime,setname;
        if(e.currentTarget){
            setname=util.$_getData(e).setname;
            hideTime=util.$_getData(e).hideTime;
            showTime=util.$_getData(e).showTime;
        }else{
            hideTime=e.hideTime;
            showTime=e.showTime;
            setname=e.setname;

        }
        if(!setname) throw new Error('未填写setname字段')
        const that=this
        if(!that.data._animate) that.data._animate={};
        let _animate=that.data._animate[setname];


        if(_animate&&_animate.if){
            that.data._animate[setname].start=false
            that.setData({_animate:that.data._animate})
            setTimeout(()=>{
                that.data._animate[setname].if=false
                that.setData({_animate:that.data._animate})
            },hideTime||300)
        }else{
            if(_animate==undefined) {
                that.data._animate[setname]={};
            }

            that.data._animate[setname].if=true;
            that.setData({_animate:that.data._animate})
            setTimeout(()=>{
                that.data._animate[setname].start=true;
                that.setData({_animate:that.data._animate})
            },showTime||100)

        }
        // console.log(e,this,hideTime||.3)
    },
    $_call(e){
        let phoneNumber=isString(e)?e:e.currentTarget.dataset.phone
        wx.makePhoneCall({phoneNumber})
    },
    ...router,
    $_prePageToData(key,val,toPre){
        let routeArr=getCurrentPages()
        routeArr[routeArr.length-2].data[key]=val;
        if(toPre){
            wx.navigateBack({
                delta: 1,
            })
        }
    },
    throttle(fn,durTime=1500){
        let time=false;
        return function (){
            let that=this;
            let args=arguments;
            if(time){
                return false
            }else{
                fn.apply(that,args)
            }
            time=setTimeout(()=>{
                time=false
            },durTime)
        }
    }
}