import wxFn from '../animate/wx_function'
function initThisPage(){
    wxFn.getNav(this);
    if(this.data.navHeight) this.data.otherHeight+=this.data.navHeight
    if(this.data.otherHeight){
        wxFn.isIphoneX(this,this.data.otherHeight)
    }else{
        wxFn.isIphoneX(this)
    }
}
const initBasePage={
    data:{

    },
    onReady(){
        initThisPage.apply(this)
    },

}

export {initBasePage}
